
if(typeof binlyric != 'object') {let binlyric = {};}  
let binlyric = {
    edition:"1.1",  
    obj:"",  
    lyricCSS:new Object(),  
    txt:"",  
    index:0,  
    time:new Array(),  
    lyric:new Array(),  
    sort:function(){ //   
        var third;  
        for(var j=0;j<this.index-1;j++)  
        {  
            for(var i=0;i<this.index-1;i++)  
            {  
                if(this.time[i]>this.time[i+1])  
                {  
                    third = this.time[i];  
                    this.time[i] = this.time[i+1];  
                    this.time[i+1] = third;  
                    third = this.lyric[i];  
                    this.lyric[i] = this.lyric[i+1];  
                    this.lyric[i+1] = third;  
                }  
            }  
        }  
    },  
    createPanel:function(){ //   
    /*    var i=0;  
        $(this.obj).html("");  
        for(i=0;i<this.index;i++)  
        {  
            $(this.obj).append("<div>"+this.lyric[i]+"</div>");  
        }  
        for(i in this.lyricCSS)  
        {  
            $(this.obj).find("div").css(this.lyricCSS,this.lyricCSS[i]);  
            }  */
    },  
    findTags:function(index,strArray,number){ //   
        //   
        //   
        number = number || this.txt.length;  
        number = (number>this.txt.length) ? this.txt.length:number;  
        var i,j,complete=0,value;  
        var obj = new Object();  
        obj.booble = false;  
        obj.value = "[";  
        for(i=index;i<number;i++)  
        {  
            if(this.txt.substr(i,1)==strArray[complete].s)  
            {  
                complete+=1;  
                if(complete>1)  
                {  
                    if(complete<strArray.length)  
                    {  
                        obj.value += '{value:"'+this.txt.substr(value+1,i-value-1)+'"},';  
                    }  
                    else  
                    {  
                        obj.value += '{value:"'+this.txt.substr(value+1,i-value-1)+'"}]';  
                    }  
                }  
                if(complete==strArray.length)  
                {  
                    obj.txt = this.txt.substr(index,i-index+1);  
                    obj.value = eval('('+obj.value+')');  
                    obj.index = i+1;  
                    obj.booble = true;  
                    break  
                }  
                value = i;  
            }  
            else if(this.txt.substr(i,1)=="\n")  
            {  
                obj.booble = false;  
                return obj;  
            }  
            else if(this.txt.substr(i,1)==strArray[0].s && complete>0) // 2  
            {  
                obj.booble = false;  
                return obj;  
            }  
        }  
        return obj;  
    },  
    findlyric:function(index){ //      
        var obj = {};  
        var str = this.txt;  
        var i;  
        for(i=index;i<str.length;i++)  
        {  
            if(str.charAt(i)=="[")  
            {  
                var _obj = this.findTags(i,[{s:"["},{s:":"},{s:"]"}]);  
                if(_obj.booble)  
                {  
                    obj.index = i;//i + _obj.txt.length;  
                    obj.lyric = str.substr(index,i-index);  
                    return obj;  
                }  
            }  
            else if(str.charAt(i)=="\n")  
            {  
                obj.index = i+1;  
                obj.lyric = str.substr(index,i-index);  
                return obj  
            }  
        }  
        if(i==str.length) //   
        {  
            obj.index = i+1;  
            obj.lyric = str.substr(index,i-index);  
            return obj;  
        }  
        obj.index = i;  
        return obj;  
    },  
    findTime:function(index){ //       
        //  findTags   
        // findTags  findID  
        var obj = {};  
        var thisobj = this;  
        var str = this.txt;  
        obj.index = index;  
        function recursion()  
        {  
            var _obj = thisobj.findTime(obj.index);  
            if(_obj.time)  
            {  
                obj.time += _obj.time;  
                obj.index = _obj.index;  
            }  
        }
		// ---------------    ---------------
		// lrclrc   
		if(/\[\d{1,2}\:\d{1,2}\.\d{1,2}\]/.test(str.substr(index,10))) // [mm:ss.ff]
		{
			obj.time = str.substr(index+1,8).replace(/\[/g,'') + "|";
			obj.index = index+9+1;
			recursion();
		}else if(/\[\d{1,2}\:\d{1,2}\.\d{1,3}\]/.test(str.substr(index,11))){
			obj.time = str.substr(index+1,9).replace(/\[/g,'') + "|";
			obj.index = index+10+1;
			recursion();
        }
		else if(/\[\d{1,2}\:\d{1,3}\]/.test(str.substr(index,7))) // [mm:ss]
		{
			obj.time = str.substr(index+1,5) + ".00" + "|";
			obj.index = index+6+1;
			recursion();
		}
		// 
		// [mm:ss.f] [mm:s.ff] [mm:s.f] [m:ss.ff] [m:s.ff] [m:s.f]
		// [mm:s] [m:ss] [s:s]
		return obj;
    },  
    findID:function(index){ //   
        //[ar:]  
        //[ti:]  
        //[al:]  
        //[by:LRC]  
        //[offset:]   
        //  offset   sort   
        //   findTags   
        var obj;  
        obj = this.findTags(index,[{s:"["},{s:":"},{s:"]"}]);  
        if(obj.booble)  
        {  
            if(obj.value[0].value=="ar")  
            {  
                this.ar = obj.value[1].value;  
            }  
            else if(obj.value[0].value=="ti")  
            {  
                this.ti = obj.value[1].value;  
            }  
            else if(obj.value[0].value=="al")  
            {  
                this.al = obj.value[1].value;  
            }  
            else if(obj.value[0].value=="by")  
            {  
                this.by = obj.value[1].value;  
            }  
            else if(obj.value[0].value=="offset") //  offset   
            {  
                this.offset = obj.value[1].value;  
            }  
        }  
    },  
    analysis:function(txt){ //  
        this.txt = txt
        if(this.txt=="") return false;  
        var str = this.txt;  
        this.index = 0;  
        for(var i=0;i<str.length;i++)  
        {  
            if(str.charAt(i)=="[")
            {  
                var time = this.findTime(i);   
                if(time.time) //   
                {  
                    var lyric = this.findlyric(time.index);  
                    if(lyric.lyric!="\n" && lyric.lyric!="") //   
                    {  
                        var timeArray = time.time.split("|");  
                        for(var j=0;j<timeArray.length;j++)  
                        {  
                            if(timeArray[j])  
                            {  
                                this.time[this.index] = timeArray[j];  
                                this.lyric[this.index] = lyric.lyric;  
                                this.index+=1;  
                            }  
                        }  
                    }  
                    i = time.index;  
                }  
                else //   
                {  
                    this.findID(i);  
                }  
            }  
        }  
        this.sort();  
        this.createPanel();  
    },  
    play:function(position,CSS){ //   
        /*var time;  
        var obj = this;  
        function set(index)  
        {
            var height = parseInt($(obj.obj).find("div").css("height"));  
            var top = parseInt($(obj.obj).find("div").css("margin-top"));  
            $(obj.obj).animate({  
                scrollTop:(index*height+index*top-parseInt($(obj.obj).css("height"))/2+height/2)  
            },300);  
            for(var i in CSS)  
            {  
                $(obj.obj).find("div").eq(index).css(CSS,CSS[i]);  
            }  
        }  
        for(var i=0;i<this.index;i++)  
        {  
            if(position==this.time[i])  
            {  
                Set(i);  
                return;  
            }  
            else if(position>this.time[i])  
            {  
                time = i;  
            }  
        }  
        set(time);//    */
    }  
};  
  
binlyric.obj = ".lyricPanel";  
binlyric.lyricCSS = {"font-size":"16px","margin-top":"15px","text-align":"center"};  
/*alert(""+binlyric.ar);  
alert(""+binlyric.al);  
alert(""+binlyric.by);  
alert(""+binlyric.ti);*/
export default binlyric


